import React from 'react';
import './App.css';
import 'typeface-roboto';
import Container from '@material-ui/core/Container';
import CssBaseline from '@material-ui/core/CssBaseline';
import Typography from '@material-ui/core/Typography';
import AppBar from '@material-ui/core/AppBar';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import Grid from '@material-ui/core/Grid';
import CircularProgress from '@material-ui/core/CircularProgress';
import ReCAPTCHA from "react-google-recaptcha";
import queryString from 'query-string';

var sprintf = require('sprintf-js').sprintf;


const reportUrl = 'https://api.sovetromantica.com/api/v1/report/%s/%s/%s';

class App extends React.Component {


    constructor(props) {
        super(props);
        let params = queryString.parse(window.location.search);
        this.state = {
            comment: '',
            error: false,
            buttonDisabled: true,
            recaptchakey: '6LchoqgUAAAAADbhfoFRoONyXZ01UBcfY4-gYeGm',
            token: '',
            titleId: params.title_id,
            type: params.type,
            num: params.num,
            time: params.time,
            mode: 'form',
            sending: false,
        };
        if (this.state.titleId == undefined || this.state.num == undefined || this.state.type == undefined || this.state.time == undefined) {
            this.state.mode = 'error';
        }
        this.submitForm = this.submitForm.bind(this);
        this.changeComment = this.changeComment.bind(this);
        this.captchaFinish = this.captchaFinish.bind(this);
        this.closeModal = this.closeModal.bind(this);
    }

    componentDidMount() {
        setTimeout((e) => window.parent.postMessage("resize::" + document.body.clientHeight, "*"), 300);
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        window.parent.postMessage("resize::" + document.body.clientHeight, "*")
    }

    async submitForm(e) {
        if (!this.state.sending) {
            if (this.state.comment === '') {
                this.setState({error: true});
            } else if (this.state.titleId == undefined || this.state.num == undefined || this.state.type == undefined || this.state.time == undefined) {
                this.setState({mode: 'error'});
            } else {
                this.setState({sending: true})
                let data=new FormData();
                data.append("token", this.state.token);
                data.append("comment", this.state.comment);
                data.append("time", this.state.time);
                let resp = await fetch(sprintf(reportUrl, this.state.titleId, this.state.type, this.state.num), {
                    method: 'POST',
                    body: data
                });
                if (resp.ok){
                    this.setState({mode:"success"});
                } else {
                    this.setState({mode:"error_send"})
                }

            }
        }

    }

    changeComment(e) {
        this.setState({
            comment: e.target.value,
            error: false,
            buttonDisabled: (this.state.token === '' || e.target.value === '')
        });
    }

    captchaFinish(e) {
        this.setState({token: e, buttonDisabled: (this.state.comment === '')});
    }

    closeModal(e) {
        window.parent.postMessage("close", "*");
    }

    render() {
        return (
            <Container>
                <CssBaseline/>
                <AppBar color="primary" classes={{colorPrimary: "main_header"}} position="sticky">
                    <Typography variant="h5" align="center">Сообщение об ошибке</Typography>
                </AppBar>
                <Grid style={{"display": (this.state.mode === 'form') ? null : 'none'}}>
                    <Typography variant="body1" align="center">Нашли ошибку в субтитрах или озвучке?</Typography>
                    <Typography variant="body1" align="center">Опишите её поподробней!</Typography>
                    <TextField style={{"marginTop": "15px"}} autoFocus={true} multiline={true} fullWidth
                               helperText="Введите описание ошибки (обязательное поле)" required rows="3"
                               variant="outlined"
                               placeholder="Комментарий" id="comment_field" value={this.state.comment}
                               onChange={this.changeComment} error={this.state.error}>

                    </TextField>
                    <ReCAPTCHA sitekey={this.state.recaptchakey} size="normal" style={{"marginTop": "15px"}}
                               onChange={this.captchaFinish}/>
                    <Grid container justify="space-around">
                        <Button size="large" variant="contained"
                                style={{"marginTop": "15px", "marginBottom": "15px","width":"130px"}}
                                disabled={this.state.buttonDisabled || this.state.sending}
                                onClick={this.submitForm}>
                            {this.state.sending ? <CircularProgress size="20px"/> : <Typography>Отправить</Typography>}
                        </Button>

                        <Button size="large" variant="contained" style={{"marginTop": "15px", "marginBottom": "15px","width":"130px"}}
                                onClick={this.closeModal}>Закрыть</Button>
                    </Grid>
                </Grid>


                <Grid style={{"display": (this.state.mode === 'error') ? null : 'none'}}>
                    <Typography variant="body1" align="center" style={{"marginTop": "15px"}}>Неверные параметры
                        формы.</Typography>
                    <Grid container justify="center">
                        <Button size="large" variant="contained" style={{"marginTop": "15px", "marginBottom": "15px"}}
                                onClick={this.closeModal}>Закрыть</Button>
                    </Grid>
                </Grid>

                <Grid style={{"display": (this.state.mode === 'success') ? null : 'none'}}>
                    <Typography variant="body1" align="center" style={{"marginTop": "15px"}}>Спасибо за
                        внимательность!</Typography>
                    <Grid container justify="center">
                        <Button size="large" variant="contained" style={{"marginTop": "15px", "marginBottom": "15px"}}
                                onClick={this.closeModal}>Закрыть</Button>
                    </Grid>
                </Grid>

                <Grid style={{"display": (this.state.mode === 'error_send') ? null : 'none'}}>
                    <Typography variant="body1" align="center" style={{"marginTop": "15px"}}>Произошла ошибка при
                        отправке</Typography>
                    <Grid container justify="center">
                        <Button size="large" variant="contained" style={{"marginTop": "15px", "marginBottom": "15px"}}
                                onClick={this.closeModal}>Закрыть</Button>
                    </Grid>
                </Grid>


            </Container>

        );
    }

}

export default App;
